enum AppState {
  START,
  QUESTION,
  RESULT,
}

const App: React.FC = () => {
  const [appState, setAppState] = useState<AppState>(AppState.START);
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [answers, setAnswers] = useState<Answer[]>([]);
  const [totalScore, setTotalScore] = useState(0);
  const [questions, setQuestions] = useState<Question[]>([]);

  useEffect(() => {
    const shuffled = [...sampleQuestions].sort(() => 0.5 - Math.random());
    setQuestions(shuffled); // 
  }, []);

  const handleStart = () => {
    setAppState(AppState.QUESTION);
    setCurrentQuestionIndex(0);
    setAnswers([]);
    setTotalScore(0);
  };

  const handleAnswer = (answer: Answer) => {
    const newAnswers = [...answers, answer];
    setAnswers(newAnswers);
    
    const newTotalScore = totalScore + (answer.answer ? questions[currentQuestionIndex].yesScore : questions[currentQuestionIndex].noScore);
    setTotalScore(newTotalScore);
    
    if (currentQuestionIndex < questions.length - 1) {
      setCurrentQuestionIndex(currentQuestionIndex + 1);
    } else {
      setAppState(AppState.RESULT);
    }
  };

  const renderContent = () => {
    switch (appState) {
      case AppState.START:
        return <StartScreen onStart={handleStart} />;
      case AppState.QUESTION:
        return questions.length > 0 ? (
          <QuestionScreen
            question={questions[currentQuestionIndex]}
            currentQuestionNumber={currentQuestionIndex + 1}
            totalQuestions={questions.length}
            onAnswer={handleAnswer}
          />
        ) : (
          <Box textAlign="center" p={8}>...</Box>
        );
      case AppState.RESULT:
        return (
          <ResultScreen
            answers={answers}
            questions={questions}
            totalScore={totalScore}
            onRestart={handleStart}
          />
        );
      default:
        return <Box textAlign="center" p={8}></Box>;
    }
  };

  return (
    <ChakraProvider>
      <Container maxW="container.lg" py={8}>
        {renderContent()}
      </Container>
    </ChakraProvider>
  );
};

export default App; 