import React, { useState } from 'react';
import { Question, Answer } from '../types/types';
import {
  Box,
  VStack,
  Heading,
  Text,
  Button,
  useToast,
  Badge,
  Tabs,
  TabList,
  TabPanels,
  Tab,
  TabPanel,
  Card,
  CardBody,
  CardHeader,
  Divider,
} from '@chakra-ui/react';

interface Props {
  answers: Answer[];
  questions: Question[];
  totalScore: number;
  onRestart: () => void;
}

const ResultScreen: React.FC<Props> = ({ answers, questions, totalScore, onRestart }) => {
  const toast = useToast();
  const [activeTab, setActiveTab] = useState(0);

  const generateResultText = () => {
    if (totalScore > 10) {
      return "SaaS";
    } else if (totalScore > 0) {
      return "Web";
    } else {
      return "";
    }
  };

  const generateAIPrompt = () => {
    const answeredQuestions = answers.map(answer => {
      const question = questions.find(q => q.id === answer.questionId);
      return {
        type: question?.type,
        answer: answer.answer,
        text: question?.questionText
      };
    });

    const businessAnswers = answeredQuestions.filter(q => q.type === 'business');
    const techAnswers = answeredQuestions.filter(q => q.type === 'tech');
    const targetAnswers = answeredQuestions.filter(q => q.type === 'target');
    const featureAnswers = answeredQuestions.filter(q => q.type === 'feature');

    return `


${businessAnswers.map(q => `- ${q.text}: ${q.answer ? '' : ''}`).join('\n')}


${techAnswers.map(q => `- ${q.text}: ${q.answer ? '' : ''}`).join('\n')}


${targetAnswers.map(q => `- ${q.text}: ${q.answer ? '' : ''}`).join('\n')}


${featureAnswers.map(q => `- ${q.text}: ${q.answer ? '' : ''}`).join('\n')}


1. SaaSWeb
2. 
3. 
4. 
5. `;
  };

  const resultText = generateResultText();
  const aiPrompt = generateAIPrompt();

  const copyToClipboard = (text: string) => {
    navigator.clipboard.writeText(text)
      .then(() => {
        toast({
          title: "",
          status: "success",
          duration: 2000,
          isClosable: true,
        });
      })
      .catch(err => {
        toast({
          title: "",
          status: "error",
          duration: 2000,
          isClosable: true,
        });
      });
  };

  return (
    <VStack spacing={6} align="stretch">
      <Card>
        <CardHeader>
          <Heading size="lg" textAlign="center"></Heading>
        </CardHeader>
        <CardBody>
          <VStack spacing={4}>
            <Badge colorScheme="green" fontSize="md" p={2}>
              : {totalScore}
            </Badge>
            <Text fontSize="lg">{resultText}</Text>
          </VStack>
        </CardBody>
      </Card>

      <Tabs isFitted variant="enclosed" onChange={(index) => setActiveTab(index)}>
        <TabList mb="1em">
          <Tab></Tab>
          <Tab>AI</Tab>
        </TabList>
        <TabPanels>
          <TabPanel>
            <VStack spacing={4} align="stretch">
              <Text fontSize="md">
                {resultText}
              </Text>
              <Button colorScheme="blue" onClick={() => copyToClipboard(resultText)}>
                
              </Button>
            </VStack>
          </TabPanel>
          <TabPanel>
            <VStack spacing={4} align="stretch">
              <Text fontSize="md" whiteSpace="pre-line">
                {aiPrompt}
              </Text>
              <Button colorScheme="green" onClick={() => copyToClipboard(aiPrompt)}>
                AI
              </Button>
            </VStack>
          </TabPanel>
        </TabPanels>
      </Tabs>

      <Divider my={4} />

      <Button colorScheme="gray" onClick={onRestart}>
        
      </Button>
    </VStack>
  );
};

export default ResultScreen; 